import React from "react";
import IconFont from "../IconFont";
import Button from "../Button";
import List from "../List";
import Router from "next/router";
import styles from "./index.less";

const reactComponent = props => {
  const {
    value: { list = [], info = {} }
  } = props;

  const number = 12;

  const goCategory = ele => {
    if (ele.status === "custom") {
      const type = ele.type === "animate" ? "allAnimate" : "allComic";
      Router.push(`/${type}`);
    } else {
      Router.push(`/cate/${ele.type}/${ele._id}`);
    }
  };

  useEffect(() => {
    const width = window.outerWidth;

    const number = width >= 1600 ? 16 : 12;
  });

  return (
    <div className={styles.indexList}>
      <div className={styles.head}>
        <div className={styles.left}>
          <IconFont type={info.icon} />
          <span>{info.name}</span>
        </div>
        <Button
          onChange={() => {
            goCategory(info);
          }}
        >
          加载更多
        </Button>
      </div>
      <List type={info.type} list={list.slice(0, number)} />
    </div>
  );
};

export default reactComponent;
